.button {
  cursor: pointer;
  text-decoration: none;
  display: block;
  border: none;
 
  background-color: var(--main-blue);
  width: 100%;
  padding: 10px 0;
  text-align: center;
  margin-bottom: 15px; 
   
  max-width: 280px;
  border-radius: 8px;
  font-size: 15px;
  color: var(--main-white);
  font-weight: 500;
  line-height: 15px;
  &__signup_link{
    background-color: var(--main-white);
    color: #05f;
    margin-top: 10px;
    color:none;
    border:none;
    margin-left:95px;

  }
  &__profile_link{
    background-color: var(--main-white);
    color: #05f;
    margin-top: 10px;
    color:none;
    border:none
  }
  &__chat_link{
    color: #05f;
    margin-top: 20px;
    margin-bottom: 20px;
    color:none;
    border:none
  }
  &_disable {
    opacity: 0.2;
  }
  &__edit_pro{ 
    margin-left: 100px;
  }
  &__edit_password{ 
    margin-left: 70px;    
  }
  &__footer-btn-User{
    max-width: 280px;
    border-radius: 8px;
    border-color: var(--main-blue);
    font-size: 15px;
    color: var(--main-blue);
  }
  &__footer-btn-send {
    box-sizing: border-box;
    cursor: pointer;
    background-color: var(--main-blue);
    border: none;
    padding: 0;
    transform: scaleX(-1);
    border-radius: 50%;
    min-width: 30px;
    height: 30px;
    background: center no-repeat url("../../image/Group 202.svg");
  }
  &__plus {
    box-sizing: border-box;
    cursor: pointer;
    background-color: var(--main-blue);
    border: none;
    padding: 0;
    border-radius: 50%;
    min-width: 30px;
    height: 30px;
    background: center no-repeat url("../../image/plus.svg");
  }
}
 